import config from '../../components/config/config.vue'
import luPopupWrapper from "@/components/lu-popup-wrapper/lu-popup-wrapper.vue";
import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
export default{
	mixins: [MescrollMixin], // 使用mixin (在main.js注册全局组件)
	components: {
		 luPopupWrapper
		},
	data(){
		return{
			list:[],
			CustomBar: this.CustomBar,
			mescroll: null,
			downOption: {
				auto: false, // 不自动加载
				use: false
			},
			upOption: {
				auto: false, // 不自动加载
				noMoreSize: 1, //如果列表已无数据,可设置列表的总数量要大于半页才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看; 默认5
				empty: {
					tip: '暂无相关数据' // 提示
					}
				},
				dataList: [], //列表数据
				isInit: false, // 列表是否已经初始化
				scrollY: 0,																										
		}
	},
	onLoad:function (option)  {			

	},
	mounted() {
				this.mescroll.triggerDownScroll();
			},
	methods: {
				// mescroll组件初始化的回调,可获取到mescroll对象
				mescrollInit(mescroll) {
					this.mescroll = mescroll;
				},
				/*下拉刷新的回调 */
				downCallback() {
					// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
					// loadSwiper();
					// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 page.num=1, 再触发upCallback方法 )
					this.dataList = []
					this.mescroll.resetUpScroll()
				},
				/*上拉加载的回调: 其中page.num:当前页 从1开始, page.size:每页数据条数,默认10 */
				upCallback() {
					this.getdata()
	
				},						
				getdata(){
					let  curPageData = []
					var value = uni.getStorageSync('token');
					if(value){				
					  	uni.request({
					  	    url: config.httpUrl +'/api/v1/commodity/orderuserlist',
					  		method:"GET",
							data:{
								'type':'finish'
							},
					  	    header: {
					  		   'Authorization' : value,
					  	    },
					  	    success: (res) => {
								var jsondata=res.data
					  			if(jsondata.code=='0'){
									
									for (var i = 0;i<jsondata.data.length; i++) {
										curPageData.push({
											oid:jsondata.data[i]["oid"],
											state:jsondata.data[i]["state"],
											mid:jsondata.data[i]["mid"],
											mname:jsondata.data[i]["mname"],
											time:jsondata.data[i]["time"],
											sname:jsondata.data[i]["sname"],
											sphone:jsondata.data[i]["sphone"],
											saddress:jsondata.data[i]["saddress"],
											sdetail:jsondata.data[i]["sdetail"],
											rname:jsondata.data[i]["rname"],
											rphone:jsondata.data[i]["rphone"],
											raddress:jsondata.data[i]["raddress"],
											rdetail:jsondata.data[i]["rdetail"],
											thingtype:jsondata.data[i]["thingtype"],
											thingweight:jsondata.data[i]["thingweight"],
											thingmess:jsondata.data[i]["thingmess"],
										})
									}
									
									this.list = curPageData;
									//分页加载 暂时不用
									this.mescroll.endByPage(curPageData.length,0);																																
					  			}else{
					  				 config.txtshow(jsondata.msg)				  				
					  			}	
															
					  	    },
					  		fail: () =>{  
					  			 config.txtshow('网络异常')
					  	     }, 
					  	});
					  }else{
					  	//跳转到登录界面
					  	uni.navigateTo({
					      url: '../user/login'
					  });
					    
					}
				},
				transportmess(item){
					var getdata=this.getdata
					uni.showActionSheet({
					    itemList:['与运货商交流','查看历史物流信息'],
					    success: function (res) {
							if(res.tapIndex==0){
								uni.navigateTo({
									url:'../user/chat?id='+item.mid+'&name='+item.mname
								})
							}else if(res.tapIndex==1){
								uni.navigateTo({
									url:'userlogisticsTrack?id='+item.oid
								})
							}else {}
					    },
					    fail: function (res) {
							//不进行任何操作
					        // console.log(res.errMsg);
					    }
					});	
				}
	
															
			},
}
